
@import url(https://fonts.googleapis.com/css?family=Montserrat);
body {
  margin: 0;
  font-family: 'Montserrat',sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1 {  
  font-size: 4rem;
  font-weight: bold;
}
h2{
  font-weight: 900;
}
h6 {
  font-size: 1rem;
  font-weight: 800;
  color: #089d82;
  letter-spacing: 1px;
}
h5 {
  font-size: 1.5rem;
  font-weight: 900;
  color: #41464b;
}
p{
  font-size: 17px;
    line-height: 1.75rem;
    letter-spacing: 0.5px;
}
a{
  text-decoration: none;
}
a:hover{
  color:#fad3aa;
}