/* educ col */
.school-col{
    padding: 10px 24px;
}
.educ{
    box-shadow: 0 3px 10px rgb(0 0 0 / 20%);
    margin-left: 3rem;
    padding: 2rem;
    border-radius:10px;
    background: url('/public/images/bg4.jpg');
    background-size: cover;
    background-position: center;
}
.course {
    font-size: 18px;
    font-weight: 600;
}
.educ-heading{
    text-align: center;
}
.school-name {
    font-size: 18px;
    color: #1b98b8;
}
.year {
    font-size: 15px;
    color: #f28b1f;
    font-weight: 700; 
}
.school-logo {
    text-align: center;
    padding: 20px 0px;
}
.school-img {
    width: auto;
    height: 200px;
}

/* experience */
.experience-sec{
    padding: 100px 0px;
    background-size: cover;
}
.col-exp-main {
    padding: 0px 50px;
}
.exp-title h3 {
    font-size: 20px;
    color: #1b98b8;
}
.exp-company h4{
    font-size: 18px;
    color: #f28b1f;
}
.exp-work-text p {
    font-size: 16px;
}
.exp-col{
    padding: 20px 0px;
}
.me-img {
    width: 90%;
}
.btn-proj{
    text-align: center;
}

/* Projects */
.project-sec{
    padding: 100px 0px;
    background: url('/public/images/bg5.jpg');
    background-size: cover;
}
.project-col {
    box-shadow: 0 3px 10pxrgba(0,0,0,.2);
    border: 2px solid #d1ffff;
}
.proj-row{
    padding: 16px 0px;
}
.proj-row a{
    border-radius: 10px;
}


.pj-img {
    width: 100%;
}
.proj-con{
padding: 15px 10px;
background-color:#1b98b8;
text-align: center;
}
.proj-title{
    color: #000;
    font-size: 18px;
}
.proj-details {
    color: #fff;
    margin-bottom: 1px !important;
    font-size: 14px;
}
.proj-heading {
    text-align: center;
    padding-bottom: 50px;
}
.proj-heading h2 {
color:#1b98b8;
}
.proj-heading-sub{
    padding-top: 50px;
    text-align: center;
}

