}

/* mobile responsive */

@media only screen and (max-width: 767px) {

    .educ {
        margin-left: 0rem;
        margin-top: 1rem;
    }
    h1 {
        font-size: 45px;
        font-weight: bold;
    }
    p {
        font-size: 15px;
    }
    h2 {
        text-align: center;
    }
    .biography-sec ,.skills-sec ,.experience-sec ,.project-sec {
        padding: 50px 10px;
    }
    .abt-img {
        width: 72%;
    }
    .btn-banner {
       text-align: center;
    }
    .rainbow{
        text-align: center;
    }
    .me-img {
        width: 40%;
    }
    .project-col {
        margin: 6px 0px;
    }
   .contact-sec{
    padding: 50px 0px;
    }
    .banner-img-col {
        text-align: center;
        padding-top: 35px;
    }
    .banner-img {
        width: 50%;
    }
    div#offcanvasRight {
        width: 80%;
    }
    .col-exp-main {
        padding: 0 15px;
    }

    }